/* Header.css */

/* Reset & base */
* { box-sizing: border-box; margin: 0; padding: 0; }

:root {
  --gold:gold;
  --gold-contrast: #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

  /* Typography scale (clamp = responsive) */
  --font-sans: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  --text-size: clamp(16px, 1.6vw, 18px);   /* Base body text size — the SAME across pages */
  --h1-size: clamp(28px, 4.2vw, 40px);     /* Unified headings scale */
  --h2-size: clamp(24px, 3.4vw, 32px);
  --h3-size: clamp(20px, 2.6vw, 26px);
  --h4-size: clamp(18px, 2.2vw, 22px);
  --h5-size: clamp(17px, 2.0vw, 20px);
  --h6-size: clamp(16px, 1.8vw, 18px);

  /* Space + radius */
  --radius: 16px;
  --gap: clamp(10px, 1.5vw, 18px);
  --shadow: 0 10px 20px rgba(0,0,0,.15);
}
nav.menu-vip {
  width: 100%;
  background-color:var(--gold-contrast);
  position: fixed;
  top: 0;
  font-style: uppercase ;
  z-index: 99100;
}

/* Full-width container */
nav.menu-vip .container {
  width: 100% !important;
  max-width: none !important;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Logo */
nav.menu-vip .logo {
  display: flex;
  align-items: center;
  color: gold;
  font-size: 1.2rem;
  font-weight: bold;
}
nav.menu-vip .logo img {
  height: 3rem;
  margin-right: 10px;
}

/* Desktop nav */
.menu-vip-nav {
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.menu-vip-nav li {
  position: relative;
  margin: 0 8px; /* reduced spacing */
}
.menu-vip-nav a,
.menu-vip-nav .nav-btn {
  color: gold;
  font-style: uppercase ;
  text-decoration: none;
  font-size: 0.95rem;
  padding: 10px 4px;
  display: inline-block;
  transition: color 0.3s;
}
.menu-vip-nav a:hover,
.menu-vip-nav .nav-btn:hover {
  color: gold;
}

/* Dropdown arrow */
.menu-vip-nav .arrow {
  display: inline-block;
  margin-left: 4px;
  border: solid gold;
  border-width: 0 1px 1px 0;
  padding: 2px;
  transform: rotate(45deg);
  transition: transform 0.3s;
}
.menu-vip-nav .has-submenu .nav-btn[aria-expanded="true"] .arrow {
  transform: rotate(-135deg);
}

/* Submenu */
.submenu {
  position: absolute;
  top: 100%;
  left: 0;
  background: darkblue;
  padding: 8px 0;
  width:250px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: all 0.3s cubic-bezier(.4,0,.2,1);
  z-index: 10;
}
.has-submenu:hover .submenu{
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.submenu a {
  display: block;
  padding: 8px 16px;
  gold-space: nowrap;
}
.submenu a:hover { background: rgba(255,255,255,0.1); }

/* Language dropdown */
.lang-dropdown { margin-left: 20px; }
.lang-dropdown .dropdown-btn { background: none; border: none; cursor: pointer; color: gold; }
.dropdown-list {
  position: absolute;
  top: 100%;
  right: 0;
  background: darkblue;
  padding: 8px 0;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
}
.dropdown-list button { width: 100%; background: none; border: none; color: gold; padding: 8px 16px; text-align: left; }
.dropdown-list button:hover { background: rgba(255,255,255,0.1); }

/* Search panel */
.search-panel { position: relative; }
.search-icon-btn { background: none; border: none; color: gold; font-size: 1.2rem; cursor: pointer; }
.search-input-wrap { display: none; position: absolute; right: 0; top: 100%; width: 200px; }
.search-panel:hover .search-input-wrap { display: block; }
.search-input-wrap input {
  width: 100%;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Burger button */
.burger-btn { background: none; border: none; cursor: pointer; display: none;margin-right: 2vw; }
.burger span {
  display: block;
  width: 45px;
  height: 3px;
  background: gold;
  margin: 25px 0;
  transition: transform 0.3s, opacity 0.3s;
}
.burger.open span:nth-child(1) { transform: translateY(8px) rotate(45deg); }
.burger.open span:nth-child(2) { opacity: 0; }
.burger.open span:nth-child(3) { transform: translateY(-8px) rotate(-45deg); }
.lupa{width: 30px;height:30px;object-fit: cover;}
/* Mobile menu */
.mobile-menu-panel {
  position: fixed;
  top: 0; left: -100%;
  width: 100%; height: 100%;
  background: blue;
  overflow-y: auto;
  transition: left 0.3s;
  z-index: 200;
  padding: 20px;
}
.mobile-menu-panel.open { left: 0; }
.mobile-lang-panel {position: relative;margin-top: 9vh;margin-bottom: 5vh;text-align: center;}
.mobile-search-panel { position: absolute;bottom: 90px;width: 90vw;left: 2vh;}
.mobile-lang-btn { background: none; border: 1px solid gold; color: gold; margin-right: 28px; padding: 6px 10px; cursor: pointer;font-size: 38px; }
.mobile-search-input { width: 100%; padding: 8px; border: 1px solid #555; border-radius: 4px; }
.mobile-menu-close { background: none; border: none; color: gold; font-size: 2rem; position: absolute; top: 20px; right: 20px; cursor: pointer; }
.mobile-menu-list { list-style: none; }
.mobile-menu-list li { margin-bottom: 5px; text-align: center;}
.mobile-menu-list a, .mobile-menu-drop-btn { color: gold; text-decoration: none; font-size: 2.1rem; background: none; border: none; cursor: pointer; }
.mobile-menu-submenu { padding-left: 16px; margin-top: 6px; }
.mobile-menu-submenu {display:flex;flex-direction: column;gap: 4vh;background-color: var(--gold-contrast);padding: 90px 0;}
/* Responsive */
@media (max-width: 992px) {
  .menu-vip-nav,
  .search-panel {
    display: none;
    height: 90vh;
    padding: 2vh;
  }
  .burger-btn { display: block;width: 76px;height: 76px }
}
/* === MOBILE ADAPTATION === */
@media (max-width: 992px) {
  /* Скрываем десктопные элементы */
  .menu-vip-nav,
  .search-panel {
    display: none;
  }

  /* Показываем бургер */
  .burger-btn {
    display: block;
    width: 48px;           /* меньше, чем 76px */
    height: 48px;
    margin-right: 2vw;
  }

  /* Линии внутри бургера меньше и компактнее */
  .burger-btn span {
    width: 32px;
    height: 2.5px;
    background: gold;
    margin: 6px 0;
    transition: transform 0.3s, opacity 0.3s;
  }

  /* Открытое состояние */
  .burger.open span:nth-child(1) { transform: translateY(6px) rotate(45deg); }
  .burger.open span:nth-child(2) { opacity: 0; }
  .burger.open span:nth-child(3) { transform: translateY(-6px) rotate(-45deg); }

  /* === MOBILE MENU PANEL === */
  .mobile-menu-panel {
    padding: 40px 20px;
    background: var(--gold-contrast);
  }
.mobile-menu-submenu {
    display: flex;
    flex-direction: column;
    gap: 3vh;               /* уменьшен с 4vh */
    background-color:darkblue;
    position: absolute;
left: 25vw;    padding: 60px 40px;        /* уменьшен с 90px */
  }
  /* Кнопка закрытия — поменьше */
  .mobile-menu-close {
    font-size: 1.4rem;
    top: 16px;
    right: 16px;
  }

  /* Меню: меньше текст, но uppercase сохраняем */
  .mobile-menu-list a,
  .mobile-menu-drop-btn {
    font-size: 1.2rem;        /* уменьшен с 2.1rem */
    text-transform: uppercase;
    letter-spacing: 0.04em;
    color: gold;
    padding: 10px 0;
  }

  /* Подменю — те же размеры и uppercase */
  .mobile-menu-submenu a {
    font-size: 1rem;
    text-transform: uppercase;
  }

  /* Языковые кнопки — компактнее */
  .mobile-lang-btn {
    font-size: 1.2rem;
    padding: 4px 8px;
    margin-right: 12px;
    border: 1px solid gold;
  }

  /* Поиск в мобильной панели — не слишком крупный */
  .mobile-search-panel {
    bottom: 60px;
    width: 85vw;
    left: 50%;
    transform: translateX(-50%);
  }
  .mobile-search-input {
    font-size: 1rem;
    padding: 6px 8px;
  }
}
