.globe-wrapper {
  width: 100%;
  min-height: auto;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
/* realisticGlobe.css */

.globe-wrapper {
  position: relative;
  width: 100%;
  height: auto;
  overflow: visible;
}

.globe-canvas {
  width: 100%;
  height: 80vh;
  touch-action: pan-y !important; /* разрешает вертикальный скролл */
}

@media (max-width: 768px) {
  .globe-canvas {
    height: 70vh; /* немного ниже для видимости контента */
  }
}
