
@tailwind base;

@tailwind components;
@tailwind utilities;
@layer base {
  :root {
    --color-dark-blue: #0b1a2f;
    --color-gold: gold;
    --color-light-blue: #87ceeb;

  --gold: gold;
  --gold-contrast:  #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

  /* Typography scale (clamp = responsive) */
  --font-sans: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  --text-size: clamp(16px, 2.2vw, 22px);   /* Base body text size — the SAME across pages */
  --h1-size: clamp(28px, 4.2vw, 40px);     /* Unified headings scale */
  --h2-size: clamp(24px, 3.4vw, 32px);
  --h3-size: clamp(20px, 2.6vw, 26px);
  --h4-size: clamp(18px, 2.2vw, 22px);
  --h5-size: clamp(17px, 2.0vw, 20px);
  --h6-size: clamp(16px, 1.8vw, 18px);

  /* Space + radius */
  --radius: 16px;
  --gap: clamp(10px, 1.5vw, 18px);
  --shadow: 0 10px 20px rgba(0,0,0,.15);
    
  }
}

:root{
  --gold: gold;
  --gold-contrast:  #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

}

/* Заголовки и контейнеры (заменяет legalPages.css) */
.legal-heading{
  color: var(--gold);
  text-align:center;
  font-weight:800;
  font-size: clamp(1.8rem, 2.8vw, 2.4rem);
  letter-spacing:.04em;
  margin: 0.75rem 0 0.5rem;
}
.legal-container{
  width: 80vw;
  margin: 0 auto;
  padding: 1rem 1rem 2rem;
  color: var(--text);
}
.legal-centered{
  text-align:center;
}
.legal-subheading{
  color: var(--gold);
  font-weight:700;
  margin: 1rem 0 .5rem;
}

/* Универсальная «блочная» раскладка (ColophonPage.blochny) */
.blochny-wrapper{
  width:100%;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  gap: 1.25rem;
  padding: 1rem;
}
.blochny-block{
  display:grid;
  grid-template-columns:1fr 1fr; /* слева/справа */
  align-items: stretch;
  gap: 1rem;
  background-color:  var(--gold-contrast);
  border: 1px solid var(--gold);
  min-height: 60vh;   /* требование */
  height: auto;       /* требование */
}
.blochny-left{
background-color: blue;

}
.blochny-right {padding: auto;position: relative;height: 100%; margin: .5rem 0;padding: 3vh 1vh;vertical-align: middle; }
.blochny-right ul{ margin: .25rem 0 .5rem 1.25rem; }
.blochny-right li{ margin: .2rem 0; }

@media (max-width: 900px){
  .blochny-block{
    grid-template-columns: 1fr;
  }
}

/* Левая секция: лого над TITLE, компактная высота */
.blochny-left{
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  text-align:center;
  padding: 1rem;
  gap: 0.75rem;
  min-height: 6rem;   /* «на 40% меньше», фиксированный минимум */
}
.blochny-left .blochny-logo{
    margin-bottom: 5vh;
  width:120px; height:120px; object-fit:contain; display:block;
}
.blochny-left .blochny-title{
  margin:0;
  font-weight:800;
  letter-spacing:.06em;
  font-size: clamp(1.1rem, 2.2vw, 1.6rem);
  color: var(--gold);
}

/* Правая секция: текст */
.blochny-right{
  vertical-align: middle;
  color: var(--text);
  line-height: 1.55;
}
.blochny-right p{ margin: .5rem 0; }
.blochny-right ul{ margin: .25rem 0 .5rem 1.25rem; }
.blochny-right li{ margin: .2rem 0; }

/* Первый блок с голубым фоном (опционально) */
.blochny-first{
  color: #0a0f1c;
}
.blochny-first .blochny-right{
  color: #0a0f1c;
}

/* Вспомогательные */
a{ color: var(--gold); }
/* ПРАВАЯ КОЛОНКА: центр и по вертикали, и по горизонтали */
.blochny-right{
  display: grid;           /* ключевой момент */
  place-items: center;     /* = align-items:center + justify-items:center */
  padding: 1rem 1.25rem;
  color: var(--text);
  line-height: 1.55;
  text-align: center;      /* горизонтальное выравнивание текста */
  min-height: 100%;        /* растягиваемся по высоте строки блока */
}

/* Внутренняя обёртка — управляем шириной и переполнением */
.blochny-right-inner{
  max-width: 70ch;         /* комфортная ширина текста */
  width: 100%;
  /* При очень длинном тексте раскомментируйте следующий блок,
     чтобы включить скролл внутри правой колонки и сохранить центрирование:
  max-height: 100%;
  overflow: auto;
  */
}

.blochny-right-inner p{
  margin: .5rem 0;
}
