
/* --- размеры хаба по ширине --- */
:root { --hub-max-w: 60vw; }

.gallery-page { min-height: 120vh; } /* чтобы фон тянулся на экран */
.gallery-container {
  width:  80vw;
  margin: autossss;
}


.album-tile__img {
  width: 100%;
  aspect-ratio: 4 / 3;
  object-fit: cover;
  display: block;
}

/* на планшетах/мобилках – пусть ширина ближе к full-bleed */
@media (max-width: 1024px) {
  .gallery-container { width: min(92vw, 100%); }
  .gallery-hub__grid { grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); }
}
@media (max-width: 640px) {
  .gallery-hub__grid { grid-template-columns: 1fr 1fr; gap: 12px; }
}

/* Контейнеры */
.gallery-page { display: grid; gap: 24px; }
.gallery-container { max-width: 1120px; margin: 0 auto; padding: 0 16px; }

/* Хаб альбомов (4 окна) */

@media (max-width: 800px){ .gallery-hub__grid { grid-template-columns: 1fr; } }

.album-tile {
  position: relative; overflow: hidden; border-radius: 16px;
  border: 1px solid rgba(255,255,255,0.12);
  background: #0b0b0e;
  aspect-ratio: 16 / 9; cursor: pointer;
}
.album-tile__img { width: 100%; height: 100%; object-fit: cover; display: block; }
.album-tile__fade { position:absolute; inset:0; background: linear-gradient(180deg, transparent, rgba(0,0,0,.65)); }
.album-tile__title {
  position: absolute; left: 16px; bottom: 12px; margin: 0;
  font-weight: 700; letter-spacing: .18em; text-transform: uppercase;
}

/* Просмотрщик альбома */
.viewer { display: grid; gap: 16px; }
.viewer__head { display:flex; gap:12px; align-items:center; justify-content:space-between; }
.viewer__title { margin: 0; font-weight: 700; letter-spacing: .18em; text-transform: uppercase; }
.viewer__back { text-decoration:none; color:#c9ccd2; }
.viewer__back:hover { color:#e6e7ea; }

.viewer__stage {
  position: relative; border-radius: 16px; overflow: hidden;
  border: 1px solid rgba(255,255,255,0.12);
  background: #000;
  min-height: 360px;
  display:flex; align-items:center; justify-content:center;
}
.viewer__img {
  max-width: 100%; max-height: 72vh; object-fit: contain; display: block; cursor: pointer;
}

/* Стрелки */
.viewer__nav {
  position:absolute; top:50%; transform: translateY(-50%);
  background: rgba(0,0,0,.45); color:#e6e7ea; border:1px solid rgba(255,255,255,.18);
  padding: 10px 12px; border-radius: 12px; cursor: pointer;
}
.viewer__nav:hover { background: rgba(255,255,255,.12); }
.viewer__nav--prev { left: 10px; }
.viewer__nav--next { right: 10px; }

/* Миниатюры */
.thumbs { display:flex; gap:8px; overflow-x:auto; padding-bottom: 4px; }
.thumbs__btn {
  border: 1px solid rgba(255,255,255,0.18); border-radius: 8px; padding: 0;
  background: #0b0b0e; cursor: pointer; flex: 0 0 auto;
}
.thumbs__img { display:block; width: 96px; height: 64px; object-fit: cover; border-radius: 8px; }
.thumbs__btn--active { outline: 2px solid #e6e7ea; }

/* Пусто/ошибки */
.empty { padding: 24px; border: 1px dashed rgba(255,255,255,.2); border-radius: 12px; color:#c9ccd2; }

/* Базовая типографика (под тёмную схему, без внешних плагинов) */
:root { color-scheme: dark; }
body { background:#0b0b0e; color:#e6e7ea; margin:0; font: 14px/1.5 ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Arial;}
a { color:#c9ccd2; }
a:hover { color:#ffffff; }
