
@tailwind base;

@tailwind components;
@tailwind utilities;
@layer base {
  :root {
    --color-dark-blue: #0b1a2f;
    --color-gold: gold;
    --color-light-blue: #87ceeb;

  --gold: gold;
  --gold-contrast:  #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

  /* Typography scale (clamp = responsive) */
  --font-sans: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  --text-size: clamp(16px, 2.2vw, 22px);   /* Base body text size — the SAME across pages */
  --h1-size: clamp(28px, 4.2vw, 40px);     /* Unified headings scale */
  --h2-size: clamp(24px, 3.4vw, 32px);
  --h3-size: clamp(20px, 2.6vw, 26px);
  --h4-size: clamp(18px, 2.2vw, 22px);
  --h5-size: clamp(17px, 2.0vw, 20px);
  --h6-size: clamp(16px, 1.8vw, 18px);

  /* Space + radius */
  --radius: 16px;
  --gap: clamp(10px, 1.5vw, 18px);
  --shadow: 0 10px 20px rgba(0,0,0,.15);
    
  }
}
.booking50 {
  min-height: 100vh;
  background: #161679;
  color: var(--gold);
  display: flex;
  width: 100vw;
  flex-direction: column;
  align-items: center;
  gap: calc(var(--gap)*1.25);
  padding: calc(var(--gap)*1.5) var(--gap)
}

.corner-mini {
  position: fixed;
  top: 12px;
  left: 12px;
  width: 56px;
  height: 56px;
  object-fit: contain;
  z-index: 9999;
  pointer-events: none;
  opacity: .95
}

.t-h1,
.t-h2 {
  text-align: left;
  color: var(--gold);
  margin: 0 0 .5em
}

.t-h1 {
  font-size: clamp(28px, 4.2vw, 40px)
}

.t-h2 {
  font-size: clamp(20px, 2.6vw, 26px)
}

.tlist {
  display: flex;
  flex-direction: column;
  gap: calc(var(--gap)*1.25);
  width: 100%;
  align-items: center
}

.tblock {
  width: 80vw;
  
  margin: 0 auto;
  padding: calc(var(--gap)*1.25);
  border: 1.5px solid var(--gold);
  border-radius: var(--radius);
  background: #04063a;
  text-align: left
}

.blist {
  display: flex;
  flex-direction: column;
  gap: 10px
}

.brow {
  display: grid;
height: 65px;  grid-template-columns: 2fr 10fr;
  column-gap: 12px;
  align-items: center ;
  justify-content: start
}


.brow img{
  align-items: center;
  width: 26px;
  height: auto;
  display: block;
  margin:9px 0 0 0;padding: 0px;
  fill: gold !important
}

.btxt {
  text-align: left;padding-bottom: 9px;line-height: 1;
}
.block-image {
  float: right;
  width:5vw;
  height: auto;
  object-fit: cover;
  margin: 0 0 10px 15px; /* отступ слева, чтобы текст не прилипал */
}
