.boss-page {
  display: block;
 text-align: center;
}
.boss-hero {   text-align: center;  margin: 0; padding: 0; line-height: 0; }
.boss-hero img { display: block; width: 100%; height: 90vh; object-fit: cover; }
.boss-title { text-align: center;
  margin: 24px auto 8px; padding: 0 16px; max-width: 1100px;
  font-size: clamp(28px, 4vw, 44px);
   font-weight: 700; letter-spacing: 0.2px;
}
.boss-wrapper { margin: 0 auto; padding: 8px 16px 32px; max-width: 1100px; }
.boss-block { padding: 16px 0; border-top: 1px solid #eee; }
.boss-block:first-child { border-top: 0; }
.boss-heading { margin: 0 0 8px; font-size: clamp(20px, 2.6vw, 28px); font-weight: 700; }
.boss-text {text-align: center; margin: 0; font-size: 16px; line-height: 1.7; color: #222; }
