@tailwind base;

@tailwind components;
@tailwind utilities;
@layer base {
  :root {
    --color-dark-blue: #0b1a2f;
    --color-gold: gold;
    --color-light-blue: #87ceeb;

  --gold: gold;
  --gold-contrast:  #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

  /* Typography scale (clamp = responsive) */
  --font-sans: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  --text-size: clamp(16px, 2.2vw, 22px);   /* Base body text size — the SAME across pages */
  --h1-size: clamp(28px, 4.2vw, 40px);     /* Unified headings scale */
  --h2-size: clamp(24px, 3.4vw, 32px);
  --h3-size: clamp(20px, 2.6vw, 26px);
  --h4-size: clamp(18px, 2.2vw, 22px);
  --h5-size: clamp(17px, 2.0vw, 20px);
  --h6-size: clamp(16px, 1.8vw, 18px);

  /* Space + radius */
  --radius: 16px;
  --gap: clamp(10px, 1.5vw, 18px);
  --shadow: 0 10px 20px rgba(0,0,0,.15);
    
  }
}
.wrapper {
 padding: 60px 20px;
  color: var(--gold);
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80vw;
  margin: 0 auto;
  border-radius: 12px;
  overflow: hidden;
    border: 1px solid gold;
 min-height: 76vh;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.normal {
  direction: ltr;
}

.reverse {
  direction: rtl;
}

.image {
  width: 100%;
  height: 100%;
  direction: ltr;
}

.image img {
  width: auto;
  height: 100%;
  object-fit: cover;
  display: block;
}

.text {
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #04063a;
  color: var(--gold);
   flex: 1;
  font-size: 1.11em;
  line-height: 1.74;
  padding:4vh 2vh;
  text-align: center;
}

.text h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--gold);
}

/* Мобильная адаптация */
@media (max-width: 768px) {
body.card, .block {
    display: flex;
    flex-direction: column;
    width: 95vw;
  }

  .image,
  .text {
    width: 100%;
    max-width: 100%;
  }

  .text {
 flex: 1;
  font-size: 1.11em;
  line-height: 1.74;
  padding:4vh 2vh;
text-align: center;
}
@media (max-width: 768px) {
  body .card, .block {
    display: flex;
    flex-direction: column;
    width: 99%;
    
    margin: 0 auto 5vh;
  }

  .image, .text {
    width: 100%;
    max-width: 100%;
  }

  .text {
    font-size: clamp(19px, 2.6vw, 22px);
    line-height: 1.7;
    padding: 3.5vh 3vw;
    text-align: left;
  }
}

}
.main-logo-container {
  display: flex;
  justify-content: center;
  margin-top: 6em;
}

.main-logo-img {
  width: 270px;
  max-width: 70vw;
}
@media (max-width: 768px) {
  .card,
  .panel,
  .box,
  .tile,
  .module {
    width: 98vw !important;
    max-width: 98vw !important;
    margin-left: auto !important;
    margin-right: auto !important;
    border-width: 1.5px !important;
  }

  /* изображения внутри карточек — на всю высоту */
  .card img,
  .panel img,
  .box img,
  .tile img,
  .module img {
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
    display: block !important;
  }
}

