.scrolling-text-wrapper {
  position: absolute;
  bottom: 40px;
  width: 100%;
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  pointer-events: none;
}

.scrolling-text {
  font-size: 2.5vw;
  font-weight: bold;
  text-transform: uppercase;
  color: transparent;
  background: gold;
  background-clip: text;
  -webkit-background-clip: text;
  animation: fadeScroll 12s ease-in-out infinite;
  white-space: nowrap;
  margin-top: 36vh;
  opacity: 0;
}

@keyframes fadeScroll {
  0% {
    opacity: 0;
  }
  15% {
    opacity: 0.25;
  }
 
    50% {
        opacity: 1;
    }
 85% {
    opacity: 0.25;
  }
  100% {
    opacity: 0;
  }
}
