
  
  :root {
    --color-dark-blue: #0b1a2f;
    --color-gold: gold;
    --color-light-blue: #87ceeb;

  --gold: gold;
  --gold-contrast:  #04063a; /* for focus rings on gold */
  --blue-bg: darkblue;       /* light, pleasant blue */
  --navy: #070a65;          /* dark blue for footers */
  --gold-border: var(--gold);

  /* Typography scale (clamp = responsive) */
  --font-sans: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Inter, Arial, 'Noto Sans', 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  --text-size: clamp(16px, 2.2vw, 22px);   /* Base body text size — the SAME across pages */
  --h1-size: clamp(28px, 4.2vw, 40px);     /* Unified headings scale */
  --h2-size: clamp(24px, 3.4vw, 32px);
  --h3-size: clamp(20px, 2.6vw, 26px);
  --h4-size: clamp(18px, 2.2vw, 22px);
  --h5-size: clamp(17px, 2.0vw, 20px);
  --h6-size: clamp(16px, 1.8vw, 18px);

  /* Space + radius */
  --radius: 16px;
  --gap: clamp(10px, 1.5vw, 18px);
  --shadow: 0 10px 20px rgba(0,0,0,.15);
    
  }

/* ---------- 1) CSS Reset (lightweight) ---------- */
*, *::before, *::after {
  box-sizing: border-box;border-color: gold;
}
html, body { margin: 0; padding: 0; }
img, svg, video, canvas, audio, iframe, embed, object { display: block; max-width: 100%; height: auto; }
input, button, textarea, select { font: inherit; color: var(--gold); }

/* ---------- 2) Theme Tokens ---------- */


/* ---------- 3) Global App Base ---------- */
html, body {
  min-height: 100%;
  background: var(--blue-bg) !important;  /* LIGHT BLUE everywhere */
  color: var(--gold) !important;          /* GOLD text everywhere */
  font-family: var(--font-sans);
  line-height: 1.6;
  font-size: var(--text-size) !important; /* UNIFIED BODY TEXT SIZE */
}

#root, #__next, .app, .App { min-height: 100%; background: transparent; }

/* ---------- 4) Typography (Unified) ---------- */
h1, h2, h3, h4, h5, h6 {
  color: var(--gold) !important;
  margin: 0 0 calc(var(--gap) * 0.75);
  line-height: 1.2;
  font-weight: 700;
}
h1 { font-size: var(--h1-size) !important; }
h2 { font-size: var(--h2-size) !important; }
h3 { font-size: var(--h3-size) !important; }
h4 { font-size: var(--h4-size) !important; }
h5 { font-size: var(--h5-size) !important; }
h6 { font-size: var(--h6-size) !important; }

p, li { font-size: var(--text-size) !important; color: var(--gold) !important; }
a { color: var(--gold) !important; text-underline-offset: 3px; }
a:hover, a:focus { opacity: .9; }

/* ---------- 5) Layout helpers ---------- */
.container, .wrapper, .page, .section, main, .content {
  padding: var(--gap);
}
.card, .panel, .box, .tile, .module {
  background: transparent !important;
  border: 1.5px solid var(--gold-border) !important;
  border-radius: var(--radius);
  padding: calc(var(--gap) * 1.25);
  box-shadow: var(--shadow);
}

/* ---------- 6) Footer Styles (Dark Navy) ---------- */
footer, .Footer, #footer, .site-footer {
  background: var(--gold-contrast) !important;     /* DARK BLUE footer */
  color: var(--gold) !important;
  border-top: 2px solid var(--gold-border) !important;
  padding: calc(var(--gap) * 1.6) var(--gap);
}
footer a { color: var(--gold) !important; }

/* ---------- 7) Header/Nav (optional harmonization) ---------- */
header, .Header, .site-header, nav, .Navbar {
  background: transparent;
  color: var(--gold);
  border-bottom: 1px solid var(--gold-border);
}

/* ---------- 8) Interactive elements ---------- */
button, .btn, [type="button"], [type="submit"], [role="button"] {
  background: transparent !important;
  color: var(--gold) !important;
 
  padding: 10px 16px;
  cursor: pointer;
  transition: transform .12s ease,  ease, opacity .12s ease;
}
button:hover, .btn:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow);
}
button:active, .btn:active { transform: translateY(0); }

input, textarea, select {
  background: rgba(255,255,255,.08) !important;
  color: var(--gold) !important;
  border: 1.5px solid var(--gold-border) !important;
  border-radius: 12px;
  padding: 10px 12px;
  outline: none;
}
input::placeholder, textarea::placeholder { color: var(--gold); }

hr { border: none; border-top: 1px solid var(--gold-border) !important; opacity: .85; }

/* ---------- 9) Tables ---------- */
table { width: 100%; border-collapse: collapse; }
th, td {
  border: 1px solid var(--gold-border) !important;
  padding: 10px;
  text-align: left;
}

/* ---------- 10) Media wrappers ---------- */
figure { margin: 0; }
figcaption { color: var(--gold); opacity: .85; font-size: 0.95em; }

/* ---------- 11) Utilities ---------- */
.gold-border { border: 1.5px solid var(--gold-border) !important; }
.gold-text { color: var(--gold) !important; }
.navy-footer { background: var(--navy) !important; color: var(--gold) !important; }
.rounded { border-radius: var(--radius) !important; }
.shadow { box-shadow: var(--shadow) !important; }

/* ---------- 12) Force-overrides for common local files ---------- */
/* These selectors target known classes from your repo to ensure the theme wins. */
 .Hero, .Footer, .Menu, .Globe, .CyberSlider, .GLTFViewer, .PhilharmonicMap, .PhotoGallery, .Press, .Support, .Ensemble, .KossakHistory, .VideoPage, .CookieSettings, .PrivacyPolicy, .TermsAndConditions, .Imprint, .Colofon {
  color: var(--gold) !important;
  border-color: var(--gold-border) !important;
}
.Hero, .Section { background: transparent !important; }
.Footer * { color: var(--gold) !important; }

/* ---------- 13) Accessibility & Focus ---------- */
:focus-visible {
  outline: 2px solid var(--gold-contrast);
  outline-offset: 2px;
}

/* ---------- 14) Optional: All elements gold (extreme mode) ---------- */
/* If you need literally ALL elements to be gold (text, borders, icons), uncomment: */
/*
* { color: var(--gold) !important; border-color: var(--gold-border) !important; fill: var(--gold) !important; stroke: var(--gold) !important; }
svg * { fill: currentColor !important; stroke: currentColor !important; }
*/
h2{text-align: center;}

