/* ==== UNIVERSAL MOBILE LAYOUT (<=768px) ==== */
@media screen and (max-width: 768px) {
  /* Базовая типографика: не меньше 19px */
  html, body, p, li { font-size: clamp(19px, 2.6vw, 22px) !important; }

  /* Единые контейнеры всех страниц */
  .wrapper,
  .blochny-wrapper,
  .content,
  .page,
  .section,
  main {
    padding-left: 0.5vw;
    padding-right: 0.5vw;
  }

  /* Главные “стрейцеры”: сетки в колонку и на весь экран по высоте */
  .block,
  .blochny-block,
  .unified-block {
    display: flex !important;
    flex-direction: column !important;
    width: 98vw !important;
    max-width: 98vw !important;
    min-height: 98dvh;         /* динамическая высота в моб. браузерах */
    min-height: 98vh;          /* фолбэк */
    margin: 0 auto 5vh;
    border-width: 1px !important;
  }

  /* Левая/правая колонка становятся секциями на всю ширину */
  .image,
  .blochny-left,
  .text,
  .blochny-right {
    width: 100% !important;
    max-width: 100% !important;
  }

  /* Картинки в колонке: адаптивно по ширине */
  .image img,
  .blochny-left img {
    width: auto !important;
    height: 100% !important;
    object-fit: cover;
    display: block;
  }

  /* Текстовый блок: читаемость и отступы */
  .text,
  .blochny-right {
    font-size: clamp(19px, 2.6vw, 22px) !important;
    line-height: 1.7 !important;
    padding: 3.5vh 3vw !important;
    text-align: left; /* если нужен центр — поменяйте на center */
  }

  /* Заголовки в мобильных блоках */
  .text h2,
  .blochny-title,
  .unified-text h2 {
    font-size: clamp(22px, 5.2vw, 32px) !important;
    margin: 0 0 0.6em 0 !important;
  }

  /* Исправление фокуса на скролле вокруг Canvas/Globe */
  .globe-wrapper,
  .globe-wrapper canvas { touch-action: pan-y; }
}
/* ==== FOOTER: Mobile adaptation (<=768px) ==== */
@media (max-width: 768px) {
  footer, .site-footer, .Footer {
    padding: 18px 16px !important;                /* немного компактнее и равномернее */
  }

  /* Если футер размечен колонками */
  footer .footer-grid,
  .site-footer .footer-grid,
  .Footer .footer-grid,
  footer .footer-columns,
  .site-footer .footer-columns,
  .Footer .footer-columns {
    display: grid !important;
    grid-template-columns: 1fr !important;        /* всё в одну колонку */
    gap: 16px !important;
  }

  /* Секции футера (заголовок + список) — вертикальные отступы */
  footer .footer-section,
  .site-footer .footer-section,
  .Footer .footer-section {
    margin-bottom: 12px !important;
  }

  /* Заголовки секций — крупнее и кликабельны */
  footer .footer-title,
  .site-footer .footer-title,
  .Footer .footer-title {
    font-size: clamp(18px, 5.4vw, 22px) !important;
    line-height: 1.3 !important;
    padding: 10px 0 !important;
  }

  /* Списки ссылок — увеличиваем «тап-зоны» */
  footer nav ul,
  .site-footer nav ul,
  .Footer nav ul,
  footer .footer-links,
  .site-footer .footer-links,
  .Footer .footer-links {
    list-style: none !important;
    padding: 0 !important;
    margin: 0 !important;
  }

  footer nav li,
  .site-footer nav li,
  .Footer nav li,
  footer .footer-links li,
  .site-footer .footer-links li,
  .Footer .footer-links li {
    margin: 0 !important;
  }

  footer nav a,
  .site-footer nav a,
  .Footer nav a,
  footer .footer-links a,
  .site-footer .footer-links a,
  .Footer .footer-links a {
    display: block !important;
    padding: 12px 0 !important;                   /* 44–48px суммарной высоты строки с line-height */
    line-height: 1.35 !important;
    word-break: break-word !important;
    text-decoration: none !important;
    color: var(--gold) !important;
  }

  /* Соц-иконки/кнопки — крупнее и с отступами */
  footer .social,
  .site-footer .social,
  .Footer .social {
    display: flex !important;
    flex-wrap: wrap !important;
    gap: 12px !important;
    margin-top: 8px !important;
  }
  footer .social a,
  .site-footer .social a,
  .Footer .social a {
    width: 44px !important;
    height: 44px !important;
    align-items: center;
    justify-content: center;
    display: inline-flex !important;
    border: 1px solid var(--gold-border) !important; /* можно убрать, если нужен «чистый» текст */
  }
  footer .social img,
  .site-footer .social img,
  .Footer .social img,
  footer .social svg,
  .site-footer .social svg,
  .Footer .social svg {
    width: 22px !important;
    height: 22px !important;
  }

  /* Логотип/бренд в футере */
  footer .footer-brand img,
  .site-footer .footer-brand img,
  .Footer .footer-brand img {
    max-width: 48vw !important;
    height: auto !important;
  }

  /* Нижняя строка (копирайт/политики) — стек и центрирование */
  footer .footer-bottom,
  .site-footer .footer-bottom,
  .Footer .footer-bottom {
    display: flex !important;
    flex-direction: column !important;
    gap: 10px !important;
    align-items: center !important;
    text-align: center !important;
    margin-top: 14px !important;
  }

  /* Аккордеоны без JS (если используешь <details>) */
  footer details,
  .site-footer details,
  .Footer details {
    border: none !important;
  }
  footer details summary,
  .site-footer details summary,
  .Footer details summary {
    cursor: pointer;
    list-style: none;
    padding: 12px 0 !important;
    font-weight: 600;
  }
  footer details[open] .footer-links,
  .site-footer details[open] .footer-links,
  .Footer details[open] .footer-links {
    margin-top: 6px !important;
  }
}
